  import axios from 'axios'
  const myId = window.localStorage.getItem("myId")
	export default {
		data() {
			return {
				inputone: "",
				inputtwo: "",
				bdzhm: true,
        zsxm: true,
        myId
			}
		},
		methods: {
			bdzh() {
				axios.get('http://121.42.177.97:8080/red/pay/phone.do', {
						params: {
							user_id: this.myId,
							phone: this.inputtwo,
							real_name: this.inputone
						}
          })
					.then(response => {
						alert("成功")
					})
					.catch(function(error) {
						console.log(error);
					});
			}

		},
		mounted() {
			axios.get('http://121.42.177.97:8080/red/pay/get_pay_phone.do?user_id='+this.myId)
				.then(response => {
					console.log(response)
					if(response.data.data == null) {
					return false
					} else {
						this.inputtwo = response.data.data
						this.bdzhm = false;
						this.zsxm = false
					}
				})
				.catch(function(error) {
					console.log(error);
				});
		}
	}



